.pumpingStation {
  width: 411px;
  height: 636px;
}
.pumpingStation .yunxing {
  width: 137px;
  text-align: center;
  height: 37px;
}
.pumpingStation .yunxing p {
  font-size: 16px;
  line-height: 37px;
  letter-spacing: 1px;
  color: #ffffff;
  text-shadow: 0 0 30px #f4faf5;
}
.pumpingStation .drainagePump {
  margin-top: 10px;
  width: 411px;
  height: 589px;
  object-fit: cover;
  border-style: solid;
  border-color: transparent;
  border-width: 200px 310px 35px 65px;
  border-image: url(/images/imgs/onescreen/框.png);
  border-image-slice: 200 310 35 65 fill;
  padding: 0;
}
.pumpingStation .drainagePump .sbox {
  height: 500px;
  margin-top: -200px;
  margin-left: -65px;
  margin-right: -305px;
  margin-bottom: -15px;
}
.pumpingStation .drainagePump .sbox .bzbeijin {
  display: inline-block;
  margin-top: 49px;
  margin-left: 18px;
  width: 392px;
  height: 199px;
  /* Standard syntax */
}
.pumpingStation .drainagePump .sbox .bzbeijin .psone {
  width: 182px;
  height: 45px;
  font-size: 16px;
  color: #fff;
  text-align: center;
  letter-spacing: 1px;
  line-height: 45px;
}
.pumpingStation .drainagePump .sbox .bzbeijin .condition {
  margin-left: 45px;
  width: 290px;
  height: 36px;
}
.pumpingStation .drainagePump .sbox .bzbeijin .condition p {
  float: left;
  display: inline-block;
  height: 36px;
  line-height: 36px;
  font-size: 14px;
  color: #00ffff;
}
.pumpingStation .drainagePump .sbox .bzbeijin .condition .stop {
  margin-left: 22px;
  float: left;
  width: 17px;
  height: 33px;
}
.pumpingStation .drainagePump .sbox .bzbeijin .condition .stop span {
  display: inline-block;
  width: 17px;
  height: 22px;
}
.pumpingStation .drainagePump .sbox .bzbeijin .condition .stop i {
  font-size: 12px;
  font-style: normal;
  color: #0090ff;
}
.pumpingStation .drainagePump .sbox .bzbeijin .jindu {
  margin-top: 17px;
  margin-left: 45px;
  height: 14px;
}
.pumpingStation .drainagePump .sbox .bzbeijin .jindu .shunshi {
  width: 60px;
  height: 14px;
  float: left;
  font-size: 14px;
  color: #00ffff;
  letter-spacing: 1px;
}
.pumpingStation .drainagePump .sbox .bzbeijin .jindu .Time {
  font-size: 16px;
  color: #00ffff;
}
.pumpingStation .drainagePump .sbox .bzbeijin .jindu .Time span {
  font-size: 12px;
}
.pumpingStation .drainagePump .sbox .bzbeijin .container {
  margin-left: 20px;
  margin-right: 5px;
  float: left;
  width: 156px;
}
.pumpingStation .drainagePump .sbox .bzbeijin .title {
  margin-left: 20px;
  background: #545965;
  color: #fff;
  padding: 15px;
  float: left;
  position: relative;
  -webkit-border-top-left-radius: 5px;
  -webkit-border-bottom-left-radius: 5px;
  -moz-border-radius-topleft: 5px;
  -moz-border-radius-bottomleft: 5px;
  border-top-left-radius: 5px;
  border-bottom-left-radius: 5px;
}
.pumpingStation .drainagePump .sbox .bzbeijin .bar {
  height: 18px;
  display: block;
  background: #0647bb;
  width: 100%;
  border-radius: 8px;
}
.pumpingStation .drainagePump .sbox .bzbeijin .bar1 {
  height: 18px;
  display: block;
  background: #00ffff;
  width: 0;
  border-radius: 8px;
  -webkit-transition: width 0.8s ease;
  -moz-transition: width 0.8s ease;
  transition: width 0.8s ease;
  -webkit-animation: progressbar 7s infinite;
  animation: progressbar 7s infinite;
}
@-webkit-keyframes progressbar {
  from {
    width: 0;
  }
  to {
    width: 60%;
  }
}
@keyframes progressbar {
  from {
    width: 0;
  }
  to {
    width: 60%;
  }
}
